export default {
	data() {
		return {
			autoplay: false,
			duration: 500,
			jumpover: '跳过',
			experience: '立即体验'
		};
	},
	methods: {
		launchFlag: function() {
			/**
			 * 向本地存储中设置launchFlag的值，即启动标识；
			 */
			uni.setStorage({
				key: 'launchFlag',
				data: true
			});
			uni.switchTab({
				url: '/pages/index/index'
			});
		}
	}
};
